import React, { Component } from "react";

class QPay extends Component {	 
	render() {
		return (
            <div>
                <a href="https://webapp.getqpay.com/login">
                    <img
                        src={process.env.PUBLIC_URL + "/sponsors/qpay-inkind.png"}

                        alt="qPay"
                        resizeMode="contain"
                        style={{'height': '100px', 'width': '200px'}}
                    />
                </a>
            </div>
        );
	}
}

export default QPay;